@-webkit-keyframes show {
  0% {
    height: 0px;
    border-top-width: 0px;
  }
  100% {
    height: 229px;
    border-top-width: 1px;
  }
}
@keyframes show {
  0% {
    height: 0px;
    border-top-width: 0px;
  }
  100% {
    height: 229px;
    border-top-width: 1px;
  }
}
@-webkit-keyframes hide {
  0% {
    height: 229px;
    border-top-width: 1px;
  }
  100% {
    height: 0px;
    border-top-width: 0px;
  }
}
@keyframes hide {
  0% {
    height: 229px;
    border-top-width: 1px;
  }
  100% {
    height: 0px;
    border-top-width: 0px;
  }
}
.header {
  height: 100px;
  position: relative;
  z-index: 20;
}
.header::before {
  content: " ";
  display: table;
}
.header .header-logo {
  width: 62px;
  margin-top: 22px;
}
.header .header-logo .logo {
  display: block;
  width: 55px;
  height: 55px;
  background: url(data:image/png;base64,) no-repeat 2px 2px #ff6700;
  opacity: 1;
}
.header .header-logo .logo::after {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 55px;
  height: 55px;
  content: '';
}
.header .header-logo span {
  display: block;
  text-indent: -9999px;
}
.header .header-nav {
  width: 800px;
}
.header .header-nav .nav-list {
  display: table;
  height: 88px;
  padding: 12px 0 0 30px;
  width: 775px;
  font-size: 16px;
  margin: 0;
}
.header .header-nav .nav-list .nav-category {
  font-size: 14px;
  width: 127px;
  padding-right: 15px;
  position: relative;
}
.header .header-nav .nav-list .nav-category .link-category {
  visibility: hidden;
  display: block;
  padding: 26px 0 38px;
  text-align: right;
}
.header .header-nav .nav-list .nav-category .site-category {
  width: 234px;
  height: 460px;
  position: absolute;
  top: 88px;
  left: -92px;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
}
.header .header-nav .nav-list .nav-category .site-category .category-list {
  padding: 20px 0;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .title {
  position: relative;
  display: block;
  color: #fff;
  padding-left: 30px;
  line-height: 42px;
  height: 42px;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .title i {
  color: rbga(255, 255, 255, 0.5);
  position: absolute;
  top: 13px;
  right: 20px;
  line-height: 1;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .children {
  display: none;
  position: absolute;
  left: 234px;
  top: 0;
  width: 992px;
  height: 460px;
  background: #fff;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .children .child-list {
  float: left;
  height: 458px;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .children .child-list li {
  width: 265px;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .children .child-list li .link {
  display: block;
  padding: 18px 20px;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .children .child-list li .link img {
  width: 40px;
  height: 40px;
  margin-right: 12px;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .active .children {
  display: block;
}
.header .header-nav .nav-list .nav-category .site-category .category-list .active .title {
  background: #ff6700;
  color: #fff;
}
.header .header-nav .nav-list .nav-item .link {
  padding: 26px 10px 38px;
  display: block;
}
.header .header-nav .nav-list .nav-item .link:hover {
  color: #ff6700;
}
.header .header-nav .nav-list .nav-item .item-children {
  display: none;
}
.header .header-search {
  width: 296px;
  margin-top: 25px;
}
.header .header-search .search-form {
  width: 296px;
  height: 50px;
  position: relative;
}
.header .header-search .search-form .search-text {
  box-sizing: content-box;
  width: 223px;
  height: 48px;
  position: absolute;
  top: 0;
  right: 51px;
  padding: 0 10px;
  border: 1px solid #e0e0e0;
  outline: 0;
  line-height: 48px;
}
.header .header-search .search-form:hover .search-submit {
  border-color: #b0b0b0;
}
.header .header-search .search-form:hover .search-text {
  border-color: #b0b0b0;
}
.header .header-search .search-form .search-submit {
  width: 51px;
  height: 50px;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-left: 0;
}
.header .header-search .search-form .search-submit:hover {
  color: #fff;
  border-color: #ff6700;
  background-color: #ff7600;
}
.header .header-search .search-form .search-submit .icon-chaxun {
  font-weight: bold;
  line-height: 24px;
}
.header .header-search .search-form .search-hot-word {
  position: absolute;
  top: 14px;
  right: 62px;
  z-index: 2;
}
.header .header-search .search-form .search-hot-word .hot-word-item {
  display: inline-block;
  font-size: 12px;
  background-color: #eee;
  color: #757575;
  padding: 0 5px;
  margin-left: 5px;
}
.header .header-search .search-form .search-hot-word .hot-word-item:hover {
  color: #fff;
  background-color: #ff6700;
}
.header .header-search .search-form .keyword-list {
  position: absolute;
  top: 50px;
  left: 0;
  width: 245px;
  border: 1px solid #ff6700;
  background-color: #fff;
  border-top: 0;
  z-index: 28;
  font-size: 12px;
}
.header .header-search .search-form .keyword-list .result-list {
  margin: 0;
}
.header .header-search .search-form .keyword-list .result-list a {
  position: relative;
  display: block;
  padding: 6px 15px;
  color: #424242;
}
.header .header-search .search-form .keyword-list .result-list a span {
  position: absolute;
  top: 0;
  right: 10px;
}
.header .nav-menu {
  height: 0px;
  -webkit-transition: height 0.5s;
  transition: height 0.5s;
  position: absolute;
  overflow: hidden;
  left: 0;
  top: 100px;
  min-width: 1226px;
  border-top: 1px solid #e0e0e0;
  z-index: 20;
  text-align: center;
  background-color: #fff;
}
.header .nav-menu.nav-menu-active {
  height: 229px;
  -webkit-transition: height 0.5s;
  transition: height 0.5s;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.18);
}
.header .nav-menu :not(:first-child).ant-col-4:before {
  position: absolute;
  left: 0;
  top: 35px;
  z-index: 1;
  width: 1px;
  height: 100px;
  content: "";
  background-color: #e0e0e0;
}
.header .nav-menu .figure {
  width: 160px;
  padding-top: 35px ;
  margin-bottom: 16px;
}
.header .nav-menu .figure a {
  display: block;
  width: 160px;
  padding: 0 22px;
}
.header .nav-menu .figure a img {
  width: 160px;
  height: 110px;
}
.header .nav-menu .flags {
  position: absolute;
  top: 0;
  width: 100%;
  font-size: 12px;
}
.header .nav-menu .flags .flag {
  border: 1px solid #ff6700;
  height: 18px;
  line-height: 14px;
  color: #ff6700;
  padding: 1px 20px;
  display: inline-block;
}
.header .nav-menu .price {
  margin: 0;
  line-height: 20px;
  color: #ff6700;
}
