.site-footer .service {
  padding: 27px 0 11px;
  border-bottom: 1px solid #e0e0e0;
}
.site-footer .service li {
  width: 19.8%;
  text-align: center;
  font-size: 16px;
  border-left: 1px solid #e0e0e0;
  height: 25px;
}
.site-footer .service li:first-child {
  border: none;
}
.site-footer .service li a {
  font-size: 16px;
  line-height: 1;
}
.site-footer .service li a:hover {
  color: #ff7600;
}
.site-footer .service li a i {
  vertical-align: middle;
  margin-right: 6px;
  font-size: 24px;
}
.site-footer .links {
  padding: 40px 0;
}
.site-footer .links .col-link {
  width: 960px;
  height: 112px;
}
.site-footer .links .col-link .link-item .title {
  margin: -1px 0 26px;
  color: #424242;
}
.site-footer .links .col-link .link-item dd {
  margin: 10px 0 0;
}
.site-footer .links .col-link .link-item dd a {
  color: #757575;
}
.site-footer .links .col-link .link-item dd:hover a {
  color: #ff7600;
}
.site-footer .links .col-contact {
  width: 251px;
  border-left: 1px solid #e0e0e0;
  text-align: center;
}
.site-footer .links .col-contact .phone {
  margin: 0 0 5px;
  font-size: 22px;
  color: #ff7600;
}
.site-footer .links .col-contact .time {
  margin: 0 0 16px;
}
.site-footer .links .col-contact > a {
  display: inline-block;
  color: #ff7600;
  border: 1px solid #ff7600;
  height: 28px;
  width: 118px;
}
