.login-panel {
  position: relative;
  padding-top: 130px;
  width: 1130px;
  margin: 0 auto;
}
.login-panel .form {
  position: relative;
  width: 410px;
  height: 524px;
  margin: 0 0 0 auto;
  font-size: 14px;
  z-index: 4;
  background-color: #fff;
}
.login-panel .form .mainbox .nav-tab {
  padding: 27px 0 24px;
  text-align: center;
  font-size: 24px;
}
.login-panel .form .mainbox .nav-tab .sep {
  margin: 0 35px 0 42px;
  border: 1px solid #e0e0e0;
}
.login-panel .form .mainbox .nav-tab .link {
  color: #666;
}
.login-panel .form .mainbox .nav-tab .link:hover {
  color: #f56600;
}
.login-panel .form .mainbox .nav-tab .active {
  color: #f56600;
}
.login-panel .form .mainbox .login-area {
  width: 348px;
  margin: 0 auto;
  position: relative;
}
.login-panel .form .mainbox .login-area .input {
  margin-bottom: 14px;
  display: block;
}
.login-panel .form .mainbox .login-area .input .ant-input {
  height: 50px;
  line-height: 22px;
  padding: 13px 16px 13px 14px;
  border-radius: 0;
}
.login-panel .form .mainbox .login-area .input .ant-input:focus {
  box-shadow: none;
  border-color: #d9d9d9;
}
.login-panel .form .mainbox .login-area .input .ant-input:hover {
  border-color: #d9d9d9;
}
.login-panel .form .mainbox .login-area .input .ant-btn {
  height: 50px;
  line-height: 22px;
}
.login-panel .form .mainbox .login-area .input .ant-btn a {
  color: #003ab5;
}
.login-panel .form .mainbox .login-area .input .ant-btn:hover {
  border-color: #d9d9d9;
}
.login-panel .form .mainbox .login-area .input .ant-input-group {
  top: 0;
}
.login-panel .form .mainbox .login-area .input .ant-input-group button {
  padding: 15px 20px;
}
.login-panel .form .mainbox .login-area .login-btn {
  padding-top: 10px;
}
.login-panel .form .mainbox .login-area .login-btn .ant-btn-primary {
  border-radius: 0;
  height: 50px;
  margin-bottom: 14px;
  background-color: #ff7600;
  border-color: #ff7600;
}
.login-panel .form .mainbox .other-method {
  margin-top: -5px;
}
.login-panel .form .mainbox .other-method .pan .right a {
  color: #999;
}
.login-panel .form .mainbox .other-method .pan .right .sep {
  padding-left: 5px;
}
.login-panel .form .mainbox .others {
  position: absolute;
  top: 350px;
  width: 100%;
}
.login-panel .form .mainbox .others .title {
  border-top: 1px solid #e0e0e0;
  padding-top: 10px;
}
.login-panel .form .mainbox .others .title legend {
  font-size: 14px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0;
}
.login-panel .form .mainbox .others .links {
  padding-top: 3px;
  text-align: center;
}
.login-panel .form .mainbox .others .links a {
  display: inline-block;
  height: 30px;
  width: 30px;
  margin: 0 17px;
}
.login-panel .form .mainbox .others .links a i {
  font-size: 30px;
}
