.home-hero-container {
  margin: 0;
  position: relative;
  font-size: 12px;
}
.home-hero-container .home-hero {
  margin-bottom: 26px;
}
.home-hero-container .home-hero .home-hero-slider {
  position: relative;
}
.home-hero-container .home-hero .home-hero-slider .ant-carousel .slick-slider {
  text-align: center;
  height: 460px;
  line-height: 460px;
  background: #364d79;
  overflow: hidden;
}
.home-hero-container .home-hero .home-hero-slider .ant-carousel .slick-slider .slick-list .item {
  display: block;
}
.home-hero-container .home-hero .home-hero-slider .ant-carousel .slick-slider .slick-list .item img {
  width: 1226px !important;
  height: 460px !important;
}
.home-hero-container .home-hero .home-hero-slider .ant-carousel .slick-slider .slick-dots {
  width: 400px;
  height: 18px;
  right: 30px;
  text-align: right;
}
.home-hero-container .home-hero .home-hero-slider .ant-carousel .slick-slider .slick-dots li {
  width: 10px;
  margin: 0 5px;
}
.home-hero-container .home-hero .home-hero-slider .ant-carousel .slick-slider .slick-dots li button {
  box-sizing: content-box;
  width: 6px ;
  height: 6px ;
  border-radius: 10px;
  border: 2px solid #fff;
  border-color: rgba(255, 255, 255, 0.3);
  background-color: rgba(0, 0, 0, 0.4);
}
.home-hero-container .home-hero .home-hero-slider .button {
  position: absolute;
  width: 42px;
  height: 69px;
  text-indent: -9999px;
}
.home-hero-container .home-hero .home-hero-slider .prev {
  background: url(//i1.mifile.cn/f/i/2014/cn/icon/icon-slides.png) no-repeat -84px 50%;
  width: 42px;
  top: 50%;
  left: 234px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.home-hero-container .home-hero .home-hero-slider .prev:hover {
  background-position-x: 0;
}
.home-hero-container .home-hero .home-hero-slider .next {
  background: url(//i1.mifile.cn/f/i/2014/cn/icon/icon-slides.png) no-repeat -125px 50%;
  width: 42px;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.home-hero-container .home-hero .home-hero-slider .next:hover {
  background-position-x: -41px;
}
.home-hero-container .home-hero .home-hero-sub {
  margin-top: 14px;
}
.home-hero-container .home-hero .home-hero-sub .channel .channel-list {
  margin-bottom: 0;
  text-align: center;
  background: #5f5750;
  padding: 3px 3px;
}
.home-hero-container .home-hero .home-hero-sub .channel .channel-list li {
  width: 70px;
  height: 82px;
}
.home-hero-container .home-hero .home-hero-sub .channel .channel-list li a {
  color: rgba(255, 255, 255, 0.7);
  padding-top: 18px;
  display: block;
}
.home-hero-container .home-hero .home-hero-sub .channel .channel-list li a:hover {
  color: #fff;
}
.home-hero-container .home-hero .home-hero-sub .channel .channel-list li a i {
  display: block;
  height: 24px;
  margin-bottom: 4px;
  font-size: 24px;
}
.home-hero-container .home-hero .home-hero-sub .promo {
  float: left;
}
.home-hero-container .home-hero .home-hero-sub .promo .promo-list li {
  margin-left: 14px;
  height: 170px;
}
.home-hero-container .home-hero .home-hero-sub .promo .promo-list li:hover {
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}
.home-hero-container .home-hero .home-hero-sub .promo .promo-list li a {
  display: block;
}
.home-hero-container .home-hero .home-hero-sub .promo .promo-list li a img {
  width: 316px;
  height: 170px;
}
.home-hero-container .home-hero .banner {
  margin: 2px 0 42px;
}
